.page-container {
  background: #F8F8F7;
  min-height: 100vh;
}

.page {
  min-width: 375px;
  width: 375px;
  background: #F8F8F7;

  padding: 20px;
  margin: 0 auto;

  &__left-section {
    display: none;
  }
}

.section {
  width: 100%;
  background: #F8F8F7;

  &__main {
    position: relative;
    width: 100%;

    min-height: 633px;
    padding: 20px;
    background: #FFFFFF;
    border-radius: 20px;
  }

  &__nav {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 10px;
    width: 100%;
    margin-bottom: 20px;
  }

  &__title {
    height: 40px;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 2px;
    text-transform: uppercase;

    color: #FFFFFF;
    background: #FF868E;
    border-radius: 10px;

    padding: 5px 30px;
  }

  &__images-gallery {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 10px;
  }

  &__loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.grid {
  display: flex;
  flex-direction: column;
  gap: 10.5px;

  &__item {
    position: relative;
    border-radius: 20px;

    @for $number from 1 to 5 {
      &--#{$number} {
        grid-area: i#{$number}
      }
    }
  }
}

.card {
  width: 295px;
  height: 206.5px;

  &__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 20px;
  }

  &__hover-background {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 134, 142, 0.6);
    border-radius: 20px;
  }

  &__title {
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 34px;
    width: calc(100% - 20px);
    content: "";

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height, or 150% */
    color: #FF868E;

    background-color: #fff;
    border-radius: 10px;
    margin: 0 10px 10px;
  }

  &__button {
    position: absolute;
    width: 40px;
    height: 40px;
    top: calc(50% - 20px);
    left: calc(50% - 20px);
    border-radius: 10px;
    border: none;
    transition: all 0.3s;

    &--remove-from-fav {
      background: #FFFFFF;
      background-image: url(./images/heart-filled-FF868E.svg);
      background-size: 20px 17px;
      background-position: center;
      background-repeat: no-repeat;



      &:hover {
        background: #FF868E;
        background-image: url(./images/heart-filled-FFFFFF.svg);
        background-size: 20px 17px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    &--add-to-fav {
      background: #FFFFFF;
      background-image: url(./images/heart-FF868E.svg);
      background-size: 20px 17px;
      background-position: center;
      background-repeat: no-repeat;

      &:hover {
        background: #FF868E;
        background-image: url(./images/heart-FFFFFF.svg);
        background-size: 20px 17px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }

  &:hover &__hover-background {
    display: block;
    transition: all 0.3s;
  }
}

.card .card__hover-background {
  transition: all 0.3s;
}

.label {
  font-weight: 500;
  font-size: 10px;
  line-height: 180%;

  text-transform: uppercase;
  color: #8C8C8C;
  padding-left: 10px;
}

.select {
  box-sizing: border-box;
  appearance: none;

  width: 100%;
  background: #FFFFFF;
  border-radius: 10px;
  border: 2px solid #FFFFFF;

  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #8C8C8C;

  padding: 6px 8px;
  transition: all 0.3s;

  &:hover {
    border: 2px solid #FBE0DC;
  }

  &:focus {
    outline: none;
  }
}

.select-wrapper {
  position: relative;
  height: 40px;
  margin-left: -10px;
  margin-right: 0;

  &::after {
    position: absolute;
    height: 8px;
    width: 12px;
    right: 10px;
    bottom: 16px;
    content: "";
    background-image: url(./images/arrow-down.svg);
  }
}

.navigation {
  display: flex;
  justify-content: center;

  &__button {
    width: 122px;
    height: 40px;
    background: #FBE0DC;
    border-radius: 10px;
    border: none;

    font-weight: 500;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: #FF868E;
    text-transform: uppercase;
    transition: all 0.3s;

    &:hover {
      background: #F8F8F7;
      color: #8C8C8C;
    }

    &:active {
      background: #FF868E;
      color: #FFFFFF;
    }

    &--prev {
      background-image: url(./images/left-arrow-FF868E.svg);
      background-position: center left 34px;
      background-repeat: no-repeat;
      background-size: 7px 12px;

      padding: 12px 30px 12px 56px;
      margin-right: 51px;

      &:hover {
        background-image: url(./images/left-arrow-8C8C8C.svg);
        background-position: center left 34px;
        background-repeat: no-repeat;
        background-size: 7px 12px;
      }

      &:active {
        background-image: url(./images/left-arrow-FFFFFF.svg);
        background-position: center left 34px;
        background-repeat: no-repeat;
        background-size: 7px 12px;
      }
    }

    &--next {
      background-image: url(./images/right-arrow-FF868E.svg);
      background-position: center right 34px;
      background-repeat: no-repeat;
      background-size: 7px 12px;

      padding: 12px 56px 12px 30px;

      &:hover {
        background-image: url(./images/right-arrow-8C8C8C.svg);
        background-position: center right 34px;
        background-repeat: no-repeat;
        background-size: 7px 12px;
      }

      &:active {
        background-image: url(./images/right-arrow-FFFFFF.svg);
        background-position: center right 34px;
        background-repeat: no-repeat;
        background-size: 7px 12px;
      }
    }
  }
}

@media (min-width: 768px) {
  .page {
    width: 768px;
    padding: 30px;
  }

  .navigation__button--prev {
    margin-right: 40px;
  }

  .section {
    min-height: 964px;

    &__main {
      min-height: 894px;
    }
    &__images-gallery {
      gap: 20px;
      margin-bottom: 20px;
    }
  }

  .grid {
    display: grid;
    grid-template-columns: repeat(3, 210px);
    gap: 19px;

    &:nth-child(2n+1) {
      grid-template-rows: repeat(3, 147px);
      grid-template-areas:
        "i1 i2 i3"
        "i1 i4 i4"
        "i5 i4 i4";
    }

    &:nth-child(2n) {
      grid-template-rows: repeat(3, 147px);
      grid-template-areas:
        "i1 i2 i3"
        "i4 i4 i3"
        "i4 i4 i5";
    }

    &--less-than-4 {
      &:nth-child(2n+1) {
        grid-template-rows: repeat(2, 147px);
        grid-template-areas:
          "i1 i2 i3"
          "i1 . .";
      }

      &:nth-child(2n) {
        grid-template-rows: repeat(2, 140px);
        grid-template-areas:
          "i1 i2 i3"
          ". . i3";
      }
    }

    &--less-than-3:nth-child(2n) {
      grid-template-rows: repeat(1, 147px);
      grid-template-areas:
        "i1 i2 .";
    }
  }

  .card {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 1440px) {
  .page {
    display: grid;
    grid-template-columns: 563px 680px;
    column-gap: 137px;
    align-items: start;
    width: 1440px;
    padding: 30px;

    &__left-section {
      display: block;
      position: sticky;
      top: 30px;
    }
  }

  .page__left-section .home {
    display: block;
  }

  .section {
    min-height: 840px;
    width: 680px;

    &__main {
      min-height: 770px;
    }
  }

  .grid {
    display: grid;
    grid-template-columns: repeat(3, 200px);
    gap: 20px;

    &:nth-child(2n+1) {
      grid-template-rows: repeat(3, 140px);
      grid-template-areas:
        "i1 i2 i3"
        "i1 i4 i4"
        "i5 i4 i4";
    }

    &:nth-child(2n) {
      grid-template-rows: repeat(3, 140px);
      grid-template-areas:
        "i1 i2 i3"
        "i4 i4 i3"
        "i4 i4 i5";
    }

    &--less-than-4 {
      &:nth-child(2n+1) {
        grid-template-rows: repeat(2, 140px);
        grid-template-areas:
          "i1 i2 i3"
          "i1 . .";
      }

      &:nth-child(2n) {
        grid-template-rows: repeat(2, 140px);
        grid-template-areas:
          "i1 i2 i3"
          ". . i3";
      }
    }

    &--less-than-3:nth-child(2n) {
      grid-template-rows: repeat(1, 140px);
      grid-template-areas:
        "i1 i2 .";
    }
  }
}